import React from 'react'
import { Route, Switch, BrowserRouter as Router } from 'react-router-dom'
import Home from './Pages/Home'
import history from './Utils/History.jsx'
import './App.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import VehicleSuccess from './Pages/VehicleSuccess'



function App() {
  return (
    <div className="App">
      <Router basename={process.env.PUBLIC_URL} history={history}>
        <Switch>
          <Route exact path={'/'} component={Home}/>
          <Route exact path = {'/vehicleSuccess'} component={VehicleSuccess}/>
          <Route exact path = {'/homeSuccess'} component={VehicleSuccess}/>
          <Route exact path = {'/otherSuccess'} component={VehicleSuccess}/>
        </Switch>
      </Router>
    </div>
  )
}

export default App