import React, { Component } from 'react'
import './issueForm.css'
class IssueForm extends Component {
   constructor() {
      super();
      this.state = {
         name: "React",
         product: ""
      };
      this.handleButton = this.handleButton.bind(this);
      //this.hideComponent = this.hideComponent.bind(this);
   }

   handleButton(e) {
      this.setState({
         product: e.target.id,
      }, function () {
         this.props.hideComponent(this.state.product, this.state);
      }.bind(this));
      e.preventDefault();
   }
   
   render() {
      return (
         <div>
            <div class="container">
               <div id="servicesID" class="allForm" >
                  <div class="container">
                     <div class="row justify-content-center">
                        <div class="col-1" ></div>
                        <div class="col-8  text-center">
                           <h2>How can we help?</h2>
                        </div>
                        <div class="col-1 " >
                           <a href="" onClick={this.handleButton} id="current">
                              <div id="current" class="close-container">
                                 <div id="current" class="leftright"></div>
                                 <div id="current" class="rightleft"></div>
                              </div>
                           </a>
                        </div>

                     </div>
                     <div className="row">
                        <div class="col-md-4 col-12">
                           <div class="card shadow-sm zoom">
                              <div class="card-body">
                                 <a className="a" href="" id="home" onClick={this.handleButton}>
                                    <img id="home" class="icon_button  mx-auto" src="https://lockdoctor.ie//images/home.png" alt="" width="250" height="250" />
                                    <h1 id="home" class="card-title pricing-card-title">Home</h1>
                                 </a>
                              </div>
                           </div>
                        </div>
                        <span className="or mx-auto"></span>
                        <div class="col-md-4 col-12">
                           <div class="card shadow-sm zoom">
                              <div class="card-body">
                                 <a className="a" href="" id="vehicle" onClick={this.handleButton}>
                                    <img id="vehicle" class="icon_button  mx-auto" src="https://lockdoctor.ie//images/car.png" alt="" width="250" height="250" />
                                    <h1 id="vehicle" class="card-title pricing-card-title">Vehicle</h1>
                                 </a>
                              </div>
                           </div>
                        </div>
                        <span className="text-center mx-auto or"></span>
                        <div class="col-md-4 col-12">
                           <div class="card  shadow-sm zoom">
                              <div class="card-body">
                                 <a className="a" href="" id="other" onClick={this.handleButton}>
                                    <img id="other" class="icon_button  mx-auto" src="https://lockdoctor.ie//images/other.png" alt="" width="250" height="250" />
                                    <h1 id="other" class="card-title pricing-card-title">Other</h1>
                                 </a>
                              </div>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      )
   }
}
export default IssueForm