// libraries
import React, { Component } from 'react'
import Header from '../components/Header/Header'
import Help from '../components/Help/Help'
import About from '../components/About/About'
import Nav from '../components/transparent-nav/Navbar'
import Footer from '../components/Footer/Footer'
import OGform from '../components/OGform/OGform'
import FixedButton from '../components/FixedButton/FixedButton'
import './Home.css'
import OtherForm from '../components/OtherForm/OtherForm'
import success from '../components/success/success'

class VehicleSuccess extends Component {

    constructor() {
        super();
        this.state = {
            name: "React",
            start: false,
            vehicle: false,
            home: false,
            other: false,
            success:true
        };

        this.hideComponent = this.hideComponent.bind(this);
    }

    hideComponent(name) {
console.log(name);
        switch (name) {
            case "current":
                this.setState({
                    start: true,
                    home: false,
                    vehicle: false,
                    other: false
                });
                break;
            case "Home":
                this.setState({
                    start: false,
                    home: true,
                    vehicle: false,
                    other: false
                });
                break;
            case "Vehicle":
                this.setState({
                    start: false,
                    home: false,
                    vehicle: true,
                    other: false
                });
                break;
            case "Commercial":
                this.setState({
                    start: false,
                    home: false,
                    vehicle: false,
                    other: true,
                });
                break;
            default:
                break;
        }

        console.log(this.state);
        var Scroll = require('react-scroll');
        var scroller = Scroll.scroller;

        // Somewhere else, even another file
        scroller.scrollTo('contact', {



            spy: true,
            smooth: true,
            offset: 0,
            duration: 500,

        })
        var rand = Math.random();  
        this.setState({random:rand});
    }


    render() {
        return (
            <div>
                <Nav />
                <Header className="home" />

                <Help  hideComponent={this.hideComponent} className="help" />
                <OGform className="contact" key={this.state.random} start={this.state.start} vehicle={this.state.vehicle} home={this.state.home} other={this.state.other} success={this.state.success}/>
                <About />
                <Footer />
                <FixedButton />
            </div>
        )
    }
}
export default VehicleSuccess