import React from 'react'
import './FixedButton.css'
import { Link } from 'react-scroll';

const Button = () => {

    return (
        <div>

            <Link
                className="nav-link"
                activeClass="active"
                to="contact"
                spy={true}
                smooth={true}
                offset={-30}
                duration={500}
            ><button className="fixed-btn">Contact <span className="dot"></span></button></Link>

        </div>
    )
}
export default Button