import React, { Component } from 'react'
import DetailsForm from '../DetailsForm/DetailsForm'
import HomeForm from '../HomeForm/HomeForm'
import IssueForm from '../issueForm/issueForm'
import StartForm from '../startForm/startForm'
import TimeForm from '../TimeForm/TimeForm'
import VehicleForm from '../VehicleForm/VehicleForm'
import OtherForm from '../OtherForm/OtherForm'
import Error from '../error/error'
import Success from '../success/success'
import Axios from 'axios'
import './OGform.css'

import {withRouter} from 'react-router-dom';

class OGform extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "React",
            startForm: this.props.start,
            homeForm: this.props.home,
            issueForm: false,
            otherForm: this.props.other,
            detailsForm: false,
            timeForm: false,
            vehicleForm: this.props.vehicle,
            lastName: false,
            addDetails: false,
            address: false,
            address2: false,
            carReg: false,
            county: false,
            displayDate: false,
            email: false,
            firstName: false,
            phone: false,
            problem: false,
            product: false,
            type: false,
            vehicleMake: false,
            vehicleModel: false,
            when: false,
            zip: false,
            error: false,
            success: this.props.success
        };
        this.hideComponent = this.hideComponent.bind(this);

    }

    handleSubmit = (event, info) => {
        event.preventDefault();

        this.setState(info, this.sendMail);
    }


    sendMail = () => {
        this.setState({
            disabled: true
        });

        Axios.post('http://localhost:3030/api/email', this.state)
            .then(res => {
                if (res.data.success) {
                    this.setState({
                        disabled: false,
                        emailSent: true,
                        startForm: false,
                        issueForm: false,
                        homeForm: false,
                        vehicleForm: false,
                        otherForm: false,
                        timeForm: false,
                        detailsForm: false,
                        error:false,
                        success:true    
                    });
                    console.log(res.data);

                    if(res.data.problem=="vehicle"){
                    this.props.history.push("/vehicleSuccess");
                    }
                    if(res.data.problem=="home"){
                        this.props.history.push("/homeSuccess");
                        }
                    if(res.data.problem=="other"){
                        this.props.history.push("/otherSuccess");
                    }

                    var Scroll = require('react-scroll');
                    var scroller = Scroll.scroller;
            
                    // Somewhere else, even another file
                    scroller.scrollTo('contact', {
            
                        spy: true,
                        smooth: true,
                        offset: 0,
                        duration: 500,
            
                    })
                } else {
                    this.setState({
                        disabled: false,
                        emailSent: false,
                        startForm: false,
                        issueForm: false,
                        homeForm: false,
                        vehicleForm: false,
                        otherForm: false,
                        timeForm: false,
                        detailsForm: false
                    });
                }
            })
            .catch(err => {
                console.log(err);

                this.setState({
                    disabled: false,
                    emailSent: false
                });
            })

    }


    hideComponent(name, info) {
        console.log(name);
        switch (name) {
            case "current":
                this.setState({
                    startForm: true,
                    issueForm: false,
                    homeForm: false,
                    vehicleForm: false,
                    otherForm: false,
                    timeForm: false,
                    detailsForm: false,
                    error: false,
            success: false
                });
                break;
            case "startForm":
                this.setState({
                    startForm: false,
                    issueForm: true,
                    homeForm: false,
                    vehicleForm: false,
                    otherForm: false,
                    timeForm: false,
                    detailsForm: false,
                    error: false,
            success: false
                });
                break;
            case "home":
                this.setState({
                    startForm: false,
                    issueForm: false,
                    homeForm: true,
                    vehicleForm: false,
                    otherForm: false,
                    timeForm: false,
                    detailsForm: false,
                    error: false,
            success: false
                });
                break;
            case "vehicle":
                this.setState({
                    startForm: false,
                    issueForm: false,
                    homeForm: false,
                    vehicleForm: true,
                    otherForm: false,
                    timeForm: false,
                    detailsForm: false,
                    error: false,
            success: false
                });
                break;
            case "other":
                this.setState({
                    startForm: false,
                    issueForm: false,
                    homeForm: false,
                    vehicleForm: false,
                    otherForm: true,
                    timeForm: false,
                    detailsForm: false,
                    error: false,
            success: false
                });
                break;
            case "vehicle1":
                this.setState({
                    startForm: false,
                    issueForm: false,
                    homeForm: false,
                    vehicleForm: false,
                    otherForm: false,
                    timeForm: true,
                    detailsForm: false,
                    error: false,
            success: false
                });
                break;
            case "date":
                this.setState({
                    startForm: false,
                    issueForm: false,
                    homeForm: false,
                    vehicleForm: false,
                    otherForm: false,
                    timeForm: false,
                    detailsForm: true,
                    error: false,
            success: false
                });
                break;
            default:
        }
        this.setState(info);
        console.log(this.state)

        var Scroll = require('react-scroll');
        var scroller = Scroll.scroller;

        // Somewhere else, even another file
        scroller.scrollTo('contact', {



            spy: true,
            smooth: true,
            offset: -80,
            duration: 500,

        })
    }

    render() {

        const { startForm, issueForm, homeForm, vehicleForm, otherForm, timeForm, detailsForm, error, success } = this.state;

        return (
            <div className="formDiv" id="contact">
                <h1 className="display-4 formTitle">Book Online</h1>
                {startForm &&
                    <StartForm hideComponent={this.hideComponent} />
                }
                {issueForm &&
                    <IssueForm hideComponent={this.hideComponent} />
                }
                {homeForm &&
                    <HomeForm hideComponent={this.hideComponent} />
                }
                {vehicleForm &&
                    <VehicleForm hideComponent={this.hideComponent} />
                }
                {otherForm &&
                    <OtherForm hideComponent={this.hideComponent} />
                }
                {timeForm &&
                    <TimeForm hideComponent={this.hideComponent} />
                }
                {detailsForm &&
                    <DetailsForm hideComponent={this.hideComponent} onSub={this.handleSubmit} />
                }

                {error &&
                    <Error />
                }

                {success &&
                    <Success />
                }
                <div>
                </div>
            </div>
        );
    }
}
export default withRouter(OGform)